@import "../base/mixins";
@import "../base/variables";
@import "../base/colors";
@import "../base/fonts";

@include small-mobile-only {

  .is-pattern-p4-page-header .wp-block-media-text__content,
  .is-pattern-p4-page-header .wp-block-group:first-child {
    margin: -1rem 2.3rem 0 .5rem !important;
  }
}

@include mobile-only {
  .page-header-title {
    overflow-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
  }

  .is-pattern-p4-page-header .wp-block-media-text__content {
    .wp-block-group:first-child {
      font-size: 2.2rem !important;
      margin: 0 1.2rem 0 .5rem;
      word-break: keep-all;
    }

    p {
      font-size: 1rem !important;
    }
  }

}

@media screen and (max-width:767px) {
  .page-header-content {
    display: block !important;
    margin-top: 0;
    max-width: 97%;
  }
}

@include mobile-and-tablet-only {
  h2.page-section-header {
    font-size: calc(1.35rem + .9vw) !important;
  }

  .wp-block-media-text.is-pattern-p4-page-header {
    margin-top: -3rem;
  }

  .is-pattern-p4-page-header.has-media-on-the-right p,
  .is-pattern-p4-page-header.has-media-on-the-right .wp-block-buttons {
    padding-left: .5rem;
  }

}

@include medium-and-up {
  .wp-block-media-text.alignfull.is-pattern-p4-page-header .wp-block-group:first-child {
    width: 700px;
  }
}

@include large-and-up {
  h2.page-section-header {
    font-size: calc(1.35rem + 1vw) !important;
  }

}

//page header pattern
.wp-block-media-text.alignfull.is-pattern-p4-page-header h1.has-background {
  line-height: 1.25;
  box-shadow: none !important;
  padding-top: .2rem;
  padding-bottom: .2rem;
  padding-inline-start: 8px;
  padding-inline-end: 8px !important;
}

//hidden page title in the heade - H1 fix
.hidden-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: white;
}